@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@400&display=swap);
html {
    font-family: 'Kanit', sans-serif;
}

body {
    font-family: 'Kanit', sans-serif;
}

.flyout {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    min-height: 100vh;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.home-feature-box .fa {
    font-size: 6rem;
}

.home-feature-box span {
    display: block;
    color: #111;
    font-weight: bold;
    margin-top: 1.5rem;
}

.example-components-list li>a {
    color: #495057;
}

.example-components-list li:last-child>a {
    border-bottom: 0;
}

.example-components-list li>a .fa {
    color: rgba(0, 0, 0, .35);
    float: right;
}

.footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    color: white;
    text-align: center;
}
.text-risk-date {
    width: 150px;
}

.text-risk-dep {
    width: 250px;
}

.text-risk-file {
    width: 150px;
}

.text-risk-detail {
    white-space: nowrap;
    overflow: hidden;
    width: 350px;
    text-overflow: ellipsis;
}
